<template>
  <div class="sidebar-container">

    <div class="title">
      <span>待办任务</span>
    </div>

  </div>
</template>

<script>
export default {
  name: 'side-bar',
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .sidebar-container{
    width: $sidebarWidth;
    min-height: 100%;
    background: $sidebarColor;
    border-left: 1px #ebebeb solid;
    border-right: 1px #ebebeb solid;

    .title {
      height: 60px;
      line-height: 60px;
      text-align: center;
      color: #fff;
      background-color: $orangeColor;
    }

    .task-list {
      overflow: hidden;
      margin: 5px 0;
      .todo-task {
        margin: 18px 0 18px 20px;
        font-size: 12px;
        color: #4d4d4d;
        line-height: 14px;
        white-space: nowrap;
        box-sizing: border-box;
        .label {
          vertical-align: middle;
          display: inline-block;
          max-width: 60px;
          overflow: hidden;
          text-overflow: ellipsis;
          padding-right: 10px;
          position: relative;
          &::after{
            content: '：';
            position: absolute;
            right: 0;
          }
        }
        .number {
          vertical-align: bottom;
          display: inline-block;
          text-overflow: ellipsis;

        }
      }
    }
  }

</style>



