const getters = {
  // userpane 面板显示控制
  userQueryPaneFlag: state => state.userpane.userQueryPaneFlag,
  userInfoQueryFlag: state => state.userpane.userInfoQueryFlag,

  // permission
  name: state => state.permission.name,
  roles: state => state.permission.roles,
  token: state => state.permission.token,
  routers: state => state.permission.routers,
  subroutes: state => state.permission.subroutes,
  mainNavRouteIndex: state => state.permission.mainNavRouteIndex,
  subNavRouteIndexs: state => state.permission.subNavRouteIndexs
}
export default getters



