// import Cookies from 'js-cookie'

const userpane = {

  state: {
    // TODO 显示界面的 用户信息应该放到这里

    userQueryPaneFlag: false, // 查询面板 显示 关闭 这个作为后动flag 不能作为前置的判断条件
    userInfoQueryFlag: true // 查询面板 用户信息显示 关闭

  },

  mutations: {
    SET_QUERY_PANE: (state, userQueryPaneFlag) => {
      state.userQueryPaneFlag = userQueryPaneFlag
    },

    SET_USER_INFO_QUERY: (state, userInfoQueryFlag) => {
      state.userInfoQueryFlag = userInfoQueryFlag
    }
  },
  actions: {
    /** ---------- 查询以及呼出 以下 ---------- **/
    // 查询面板 显示 关闭
    SetQueryPane ({
      commit
    }, userQueryPaneFlag) {
      commit('SET_QUERY_PANE', userQueryPaneFlag)
      commit('SET_TEL_INFO_QUERY', false)
    }
  }
}

export default userpane



