<template>
	<section class="user-info">
    <span class="test">UserinfoQuery</span>
    <div class="user-nav">
      <div class="nav-wrap">
        <el-menu default-active="0" mode="horizontal" class="el-menu-demo" background-color="#F2F2F2" active-text-color="#5A2DFF" @select="handleSelect">
          <template v-for="(userNav, userNavIndex) in userNavs">
            <el-menu-item class="usernav-li-el" :index="userNav.id" v-bind:key="userNavIndex">{{userNav.name}}</el-menu-item>
          </template>
        </el-menu>
      </div>
      <i class="el-icon-close icon-el" v-if="!telInfoQueryFlag"  @click="handleCloseQueryPane()"></i>
    </div>
    <div class="user-panes">
      <div class="base-info" v-if="activeIndex == '0'">
          <cust-title titleName="用户资料" ></cust-title>
          <div class="user-form">
            <div class="line">
              <cust-input type="text" v-model="userName" labelName="姓名:" :disabled="trueFlag"></cust-input>
              <cust-input type="tel" v-model="userTel" labelName="手机号:" :secondFlag="trueFlag" :disabled="trueFlag"></cust-input>
            </div>
            <div class="line">
              <cust-input type="text" v-model="userSex" labelName="性别:" :disabled="trueFlag"></cust-input>
              <cust-input type="noEdit" v-model="userStaticTel" labelName="固定电话:" :secondFlag="trueFlag"></cust-input>
            </div>
            <div class="line">
              <cust-input type="text" v-model="userCardID" labelName="身份证:" :disabled="trueFlag"></cust-input>
              <cust-input type="text" v-model="userAddress" labelName="家庭住址:" :secondFlag="trueFlag" :disabled="trueFlag"></cust-input>
            </div>
          </div>
          <cust-title titleName="关系人联系"></cust-title>
          <div class="user-form">
            <div class="line">
              <cust-input type="text" v-model="userName" labelName="姓名:" :disabled="trueFlag"></cust-input>
              <cust-input type="text" v-model="userName" labelName="联系方式:" :secondFlag="trueFlag" :disabled="trueFlag"></cust-input>
            </div>
          </div>
      </div>

      <div class="base-info" v-if="activeIndex == '3'">
        UserinfoQuery333
      </div>


    </div>

	</section>
</template>

<script>
import { mapGetters } from 'vuex'
import CustTitle from '@/components/CustTitle/CustTitle'
import CustInput from '@/components/CustInput/CustInput'

export default {
  name: 'UserInfo',
  components: { CustTitle, CustInput },
  data () {
    return {
      // 第一组数据
      userData: '用户资料',

      activeIndex: '0',
      userNavs: [],
      userName: '',
      userTel: '15900590619',
      userSex: '',
      userStaticTel: '',
      userCardID: '',
      userAddress: '',
      trueFlag: true,

      gridData: [{
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-01',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }]
    }
  },

  computed: {
    ...mapGetters([
      'telInfoQueryFlag'
    ])
  },

  mounted () {
    let hasRegisterNav = [{
      id: '0',
      name: '基本信息'
    }, {
      id: '3',
      name: '管理用户'
    }]
    this.userNavs = hasRegisterNav
  },

  methods: {
    handleSelect (key, keyPath) {
      this.activeIndex = key
    },
    handleCloseQueryPane () {
      this.$store.dispatch('SetQueryPane', false).then(() => {
        console.log('---- 关闭-查询面板 ------')
      })
    },
    handleGetMore () {

    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .user-info{
    z-index: 503;
    width: 800px;
    height: 590px;
    .test{
      position: absolute;
    }
    .user-nav{
      position: relative;
      width: 100%;
      .nav-wrap{
        display: flex;
        justify-content: center;
        width: 100%;
        height: 50px;
        background: #F2F2F2;
        border-bottom: solid 1px #e6e6e6;
        .usernav-li-el{
          height: 49px;
          line-height: 49px;
        }
      }
      .user-more{
        @include vetical;
        right: 20px;
        @include getIcon("../../assets/svg/user-more.svg",9px,15px);
      }
      .icon-el{
        @include vetical;
        right: 10px;
        font-size: 30px;
        color: #aaa;
      }
    }
    .user-panes{
      position: relative;
      width: 800px;
      height: 540px;
      padding-left: 40px;
      .base-info{
        position: relative;
        width: 100%;
        height: 100%;
        .more-data{
          @include veticalAndCenter;
        }
      }
      .user-form{
        position: relative;
        width: 100%;
        .line{
          width: 100%;
          display: flex;
        }
      }
    }
  }
</style>



