<template>

  <div class="subnavbar-main">

      <el-menu :default-active="activeIndex" mode="horizontal" class="subnav-el" active-text-color="#5A2DFF">
        <template v-for="(router, routeIndex) in subroutes">
           <el-menu-item :index="router.path" :key="routeIndex" @click="handleRoute(routeIndex)">{{router.name}}</el-menu-item>
        </template>
      </el-menu>

  </div>

</template>


<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      activeIndex: ''
    }
  },
  computed: {
    ...mapGetters([
      'routers',
      'subroutes',
      'mainNavRouteIndex',
      'subNavRouteIndexs'
    ])
  },
  watch: {
    subroutes () {
      this.setRouteActive()
    }
  },
  mounted () {
    this.setRouteActive()
  },
  methods: {
    handleRoute (routeIndex) {
      let mainNavRouteIndex = this.mainNavRouteIndex
      let subNavRouteIndex = routeIndex // 改变的是subIndex mainIndex不变
      let nextPath = this.routers[mainNavRouteIndex].path + '/' + this.routers[mainNavRouteIndex].children[subNavRouteIndex].path
      this.$store.dispatch('SetNavRoute', {
        'mainNavRoute': mainNavRouteIndex,
        'subNavRoute': subNavRouteIndex
      }).then(() => {
        this.$router.push({
          path: nextPath
        })
      })
    },
    setRouteActive () {
      let self = this
      setTimeout(() => {
        let subNavRouteIndex = self.subNavRouteIndexs[self.mainNavRouteIndex]
        self.activeIndex = self.routers[self.mainNavRouteIndex].children[subNavRouteIndex].path
      }, 500)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .subnavbar-main{
    position: absolute;
    top: 0;
    left: 0;
    background: #fff;
    width: $sidebarHeight;
    height: $subnavbarHeight;
    border-bottom: solid 1px #e6e6e6;
    .subnav-el{
      @include center;
    }
  }
</style>



