<template>
    <div class="cust-input">
      <label v-text="labelName" :class="{'second-label': secondFlag}"></label>
      <div class="input-wrap">
        <input
          ref="input"
          v-if="type !== 'noEdit'"
          v-bind="$props"
          :autocomplete="autoComplete"
          @input="handleInput"
          @focus="handleFocus"
          @blur="handleBlur"
          @change="handleChange"
        />
        <div v-else class="no-edit"><span class="lineae">—</span></div>
        <span class="tel-icon" v-if="type == 'tel'" @click="handleTelCall"></span>
      </div>
    </div>
</template>

<script>
export default {
  name: 'custinput',

  data () {
    return {
      currentValue: this.value
    }
  },

  props: {
    value: [String, Number],
    labelName: String,

    placeholder: String,
    maxlength: Number,
    minlength: Number,
    readonly: Boolean,
    autofocus: Boolean,
    disabled: Boolean,
    type: {
      type: String,
      default: 'text'
    },
    secondFlag: {
      type: Boolean,
      default: false
    },
    autoComplete: {
      type: String,
      default: 'off'
    },
    telFun: Function
  },

  watch: {
    'value' (val, oldValue) {
      // console.log('----watch value')
      this.setCurrentValue(val)
    }
  },

  methods: {
    handleTelCall () {
      this.$emit('telFun')
    },
    handleBlur (event) {
      // console.log('----handleBlur')
      this.$emit('blur', event)
    },
    handleFocus (event) {
      // console.log('----handleFocus')
      this.$emit('focus', event)
    },
    handleInput (event) {
      // console.log('----handleInput')
      const value = event.target.value
      this.$emit('input', value)
      this.setCurrentValue(value)
    },
    handleChange (event) {
      // console.log('----handleChange')
      this.$emit('change', event.target.value)
    },
    setCurrentValue (value) {
      // console.log('----setCurrentValue')
      if (value === this.currentValue) {
        return
      }
      this.currentValue = value
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

.cust-input {
  display: flex;
  margin-top: 20px;

  label{
    width: 70px;
    height: 36px;
    font-size: 14px;
    color: #4A4A4A;
    line-height: 36px;
  }
  .input-wrap{
    position: relative;
    width: 200px;
    height: 36px;
    input, .no-edit{
      width: 100%;
      height: 100%;
      font-size: 14px;
      color: #4A4A4A;
      border-radius: 4px;
      border: 1px solid #d8dce5;
      padding-left: 20px;

      .lineae{
        font-size: 14px;
        color: #9B9B9B;
        line-height: 34px;
      }
    }

    .tel-icon{
      @include vetical;
      right: 14px;
      @include getIcon("../../assets/svg/call-out.svg",26px,26px);
      &.close{
        background-image: url(../../assets/svg/call-out-close.svg);
      }
    }
  }

  .second-label{
    margin-left: 68px;
  }
}
</style>



