<template>
    <div class="cust-title">
      {{titleName}}
      <div class="tool-bar">
        <slot></slot>
      </div>
    </div>
</template>

<script>
export default {
  name: 'custtitle',
  props: {
    titleName: {
      type: String,
      default: '用户信息'
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cust-title{
    position: relative;
    margin-top: 20px;
    width: 100%;
    height: 54px;
    padding-left: 12px;

    font-size: 14px;
    font-weight: bold;
    color: #4A4A4A;
    line-height: 54px;

    &:before{
      @include vetical;
      left: 0;
      content: '';
      width: 2px;
      height: 14px;
      background: $orangeColor;
    }

    .tool-bar {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
    }
  }
</style>



