<template>

    <div  class="navbar-container">

      <a class="main-web">网站</a>

      <div class="nav-main">
        <el-menu :default-active="activeIndex" mode="horizontal" background-color="#0a122b" active-text-color="#ffffff">
          <template v-for="(router, routeIndex) in routers">
             <el-menu-item v-if="!router.hidden&&router.children.length>0" @click="handleRoute(routeIndex)" :index="router.path" :key="router.path">{{router.name}}</el-menu-item>
          </template>
        </el-menu>
      </div>

      <div class="right-info">
        <div class="logout">安全退出</div>
        <b class="divide-line"></b>

        <div class="state-el">
          <span >
            {{roleValue}}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
        </div>

        <span class="user-name">您好 {{name}}</span>
      </div>

    </div>

</template>


<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      roleValue: '管理员',
      stateOptions: ['就绪', '离席', '后处理'],
      activeIndex: '',
      falseLable: false
    }
  },
  computed: {
    ...mapGetters([
      'routers',
      'mainNavRouteIndex',
      'subNavRouteIndexs',
      'name'
    ])
  },
  mounted () {
    this.activeIndex = this.routers[this.mainNavRouteIndex].path
  },
  methods: {
    handleRoute (routeIndex) {
      let mainNavRouteIndex = routeIndex // 改变的是mainIndex subIndex引用之前点击过得
      let subNavRouteIndex = this.subNavRouteIndexs[mainNavRouteIndex]
      let nextPath = this.routers[mainNavRouteIndex].path + '/' + this.routers[mainNavRouteIndex].children[subNavRouteIndex].path
      this.$store.dispatch('SetNavRoute', {
        'mainNavRoute': mainNavRouteIndex,
        'subNavRoute': subNavRouteIndex
      }).then(() => {
        this.$router.push({
          path: nextPath
        })
      })
    },
    logout () {
      this.$store.dispatch('LogOut').then(() => {
        location.reload() // 为了重新实例化vue-router对象 避免bug
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .navbar-container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
    width: $navbarWidth;
    height: $navbarHeight;
    background: $navbarColor;

    color:#aaa;

    .main-web{
      position: absolute;
      height: $navbarHeight;
      line-height: $navbarHeight;
      margin-left: 20px;
      font-size: 14px;
      color: #FFFFFF;
      opacity: 0.5;
    }

    .nav-main{

      width: $mainWidth;
      height: $navbarHeight;
      line-height: $navbarHeight;

      padding-left: $sidebarWidth;
      margin: 0 auto;
      .nav-ul{
        width: 100%;
        display: flex;

        .nav-li{
          width: 80px;
          &.selected{
            color:#fff;
          }
        }
      }
    }

    .right-info{
      position:absolute;
      right: 20px;
      top: 0;
      line-height: $navbarHeight;
      font-size: 12px;
      color: #FFFFFF;
      opacity: 0.5;
      .logout{
        float: right;
      }
      .divide-line{
        float: right;
        width: 0;
        font-size: 0;
        height: 21px;
        margin: 20px 6px 0 6px;
        border-right: #aaa 1px solid;
      }
      .user-name{
        float: right;
      }
      .state-el{
        float: right;
        margin-top: 15px;
        margin-left: 10px;
        width: 86px;
        height: 28px;
        line-height: 28px;
        text-align: center;
        font-size: 12px;
        color: #fff;
        border: #aaa 1px solid;
        border-radius: 4px;
      }
    }
  }
</style>



