<template>
	<div class="web-wrapper" >

		<navbar></navbar>

		<div class="main-container">
			<sidebar></sidebar>
			<app-main></app-main>
		</div>

    <!-- userinfo-query 单独控制 -->
		<div class="query-container" v-if="userQueryPaneFlag">
			<div class="user-wap">
				<userinfo-query v-if="userInfoQueryFlag"></userinfo-query>
			</div>
		</div>

	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import { Navbar, Sidebar, AppMain } from 'views/layout' // 这个地方引用了layout文件夹中的index文件
import UserinfoQuery from 'views/components/UserinfoQuery'

export default {
  name: 'layout',
  data () {
    return {
    }
  },
  components: {
    Navbar,
    Sidebar,
    AppMain,

    UserinfoQuery
  },
  computed: {
    ...mapGetters([
      'userQueryPaneFlag',
      'userInfoQueryFlag',
      'telInfoQueryFlag',

      'userCallInPaneFlag',
      'userInfoCallInFlag',

      'userButtonFlag',
      'userButtonStyle'
    ])
  },
  watch: {
  },
  methods: {
    handleUserPane () {
      console.log('---- handleUserPane -----')
      let self = this
      if (!self.userButtonFlag) {
        console.log('---- userButtonFlag return -----')
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

	.web-wrapper {
		@include clearfix;
		position: relative;
		width: $webWidth;
		height: $webHeight;
		background:$webColor;

		.main-container {
			display: flex;
			width: $mainWidth;
			height: $mainHeight;
			margin: 0 auto;
			background: $mainColor;
			padding-top: $navbarHeight;
		}

		.query-container, .call-container{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 520;

      display: flex;
      justify-content: center;
      align-items: center;

      .user-wap{
				display: flex;
				justify-content: center;
				border: solid 1px #e6e6e6;
				border-radius: 6px;
				background: #fff;

				box-shadow: 0 4px 48px 0 rgba(0,0,0,0.32);
		  }
		}
    .call-container{
       z-index: 530;
  	}
	}
</style>



